body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.CodePane {
  max-height: 75vh;
}

.form-field {
  margin: 10px 0 15px;
}

.form-field label {
  display: block;
  margin: 10px 0;
}

.form-field input,
.form-field textarea,
.form-field select {
  display: block;
  width: 100%;
}

.form-field p {
  margin: 10px 0;
  font-size: 0.6em;
  color: #f00;
}

.my-component-1 {
  margin: 10px 0;
}

.my-component-1 button {
  margin: 5px 10px;
}

.my-component-2 {
  display: flex;
  justify-content: center;
  margin: 25px;
}

.my-component-2 button {
  padding: 8px 20px;
  font-size: 20px;
  font-weight: bold;
}

.my-component-2 .counter {
  color: #fff;
  padding: 8px 20px;
}

.my-component-2 .counter.even {
  background: #337;
}

.my-component-2 .counter.odd {
  background: #733;
}

